import React from 'react';


class SignIn extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			signInEmail: '',
			signInPassword:''
		}
		this.onSubmitSignIn = this.onSubmitSignIn.bind(this);
		this.onSignInEmail = this.onSignInEmail.bind(this);
		this.onSignInPassword = this.onSignInPassword.bind(this);

	}

	onSignInEmail = (event) => {
		this.setState({
			signInEmail : event.target.value
		})
	}

	onSignInPassword = (event) => {
		this.setState({
			signInPassword : event.target.value
		})
	}

	onSubmitSignIn = () => {
		fetch('https://serene-wildwood-48633.herokuapp.com/signin', {
			method: 'post',
			headers: {'Content-Type':'application/json'},
			body: JSON.stringify({
				email: this.state.signInEmail,
				password: this.state.signInPassword
			})
		}).then(response => response.json())
		.then(user => {
			if(user.id){
				this.props.onRouteChange('home');
			}else{
				alert("Incorrect Email & password")
			}
		})
	}


	render() {
		const { onRouteChange } = this.props;
		return (
		<div className="pa4">
		<article className="br3 shadow-5 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw6 center">
			<main className="pa4 black-80">
			  <div className="measure ">
			    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
			      <legend className="f2 fw6 ph0 mh0">Sign In</legend>
			      <div className="mt3">
			        <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
			        <input 
			        	className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
			        	type="email" 
			        	name="email-address"  
			        	id="email-address" 
			        	onChange={this.onSignInEmail}
			        	/>
			      </div>
			      <div className="mv3">
			        <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
			        <input 
			        	className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
			        	type="password" 
			        	name="password"  
			        	id="password" 
			        	onChange={this.onSignInPassword}
			        	/>
			      </div>
			    </fieldset>
			    <div className="">
			      <input 
			      	onClick={this.onSubmitSignIn}
			      	className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" 
			      	type="submit" 
			      	value="Sign in" 
			      	/>
			    </div>
			    <div className="lh-copy mt3">
			      <p onClick={() => onRouteChange('register')} className="f6 link dim black db pointer">Register</p>
			    </div>
			  </div>
			</main>
		</article>
		</div>
		);
	}
}
	


export default SignIn;