import React from 'react';
import ApparelDetection from './ApparelDetection';
import './ApparelDetectionLists.css';


const ApparelDetectionLists = ({ imageUrl, box }) => {
	const apparelItem = box.map((item, index) => {
		return (<ApparelDetection key={index} imageUrl={imageUrl} box={item}/>);
	})
	return (
			<div className="center ma">
				<div className="absolute pa3" >
					{apparelItem}
					<img id={box.id} alt='' src={imageUrl} width="500px" height="700px"/> 
				</div>
			</div>
	);
}

export default ApparelDetectionLists;