import React, { Component } from 'react';
import './App.css';
import Particles from 'react-particles-js';
import Navigation from './components/Navigation/Navigation';
import Logo from './components/Logo/Logo';
import SignIn from './components/SignIn/SignIn';
import Register from './components/Register/Register';
import ImageLinkForm from './components/ImageLinkForm/ImageLinkForm';
import ApparelDetectionLists from './components/ApparelDetection/ApparelDetectionLists';
import 'tachyons';



const paramsOptions = {
  particles:{
      number: {
        value: 50,
        density : {
          enable: true,
          area: 800,
          factor: 1000
        }
      }
    }
  }

const intialState = {
      input: '',
      imageUrl: '',
      box: [],
      route: 'signin',
      isSignedIn: false,
      user: {
        name: '',
        email: '',
        password: '',
        enteries: 0,
        joined: ''
      }
    }

class App extends Component {

  constructor(){
    super();
    this.state = intialState;
    this.onInputChange = this.onInputChange.bind(this);
    this.onButtonSubmit = this.onButtonSubmit.bind(this);
    this.calculateApparelDetection = this.calculateApparelDetection.bind(this);
    this.showBox = this.showBox.bind(this);
    this.onRouteChange = this.onRouteChange.bind(this);
    this.loadUsers = this.loadUsers.bind(this);
  }

  calculateApparelDetection(data){
    const clarifaiApparel = data.outputs[0].data.regions.map(region => {
      return region.region_info.bounding_box;
    });
    const apparel_name = data.outputs[0].data.regions.map(region => region.data.concepts[0].name);
    const ids = data.outputs[0].data.regions.map(region => region.id);
    const apparel_box = [];
    const width = 500;
    const height = 700;
    for(let i=0;i<clarifaiApparel.length;i++){
      // let image = document.getElementById(ids[i]);
      // let width = image.width;
      // let height = image.height;
      apparel_box.push({
        leftCol : clarifaiApparel[i].left_col * width,
        topRow: clarifaiApparel[i].top_row * height,
        rightCol: width - (clarifaiApparel[i].right_col * width),
        bottomRow: height - (clarifaiApparel[i].bottom_row * height),
        apparelName: apparel_name[i],
        id: ids[i]
      })
    }
    // console.log('apparel_box: ', apparel_box);
    return apparel_box;
  }

  showBox(box){
    this.setState({box: box});
  }

  onInputChange(event){
    this.setState({input: event.target.value})
  }

  onButtonSubmit(){
    this.setState({imageUrl: this.state.input})
    fetch('https://serene-wildwood-48633.herokuapp.com/imageurl', {
      method: 'post',
      headers: {'Content-Type':'application/json'},
      body: JSON.stringify({
        input: this.state.input
      })
    }).then(response => response.json())
    .then(response => 
      this.showBox(this.calculateApparelDetection(response)))
    .catch(err => console.log(err));
  }

  onRouteChange(route){
    if(route === 'signout'){
      this.setState(intialState);
    }else if(route === 'home') {
      this.setState({isSignedIn: true});
    }
    this.setState({route: route});
  }

  loadUsers = (data) => {
    this.setState({
      user:{
        name: data.name,
        email: data.email,
        password: data.password,
        enteries: data.enteries,
        joined: data.joined
      }
    })
  }

  render(){
  const navigation = this.state.route === 'home' 
        ? (
          <div style={{display: 'flex', justifyContent: 'flex-end'}}>
            <div className="w-30"><Logo /></div>
            <div className="w-70"><Navigation isSignedIn={this.state.isSignedIn} onRouteChange={this.onRouteChange}/></div>
          </div>
          )
        : (<Navigation isSignedIn={this.state.isSignedIn} onRouteChange={this.onRouteChange}/>)
    return (
      <div className="App">
        <Particles className="particles"
          params={paramsOptions} />
        {navigation}
        {this.state.route === 'home' 
        ? <div>
            <h1 className="fashion">Fashionista Bot</h1>
            <p className='para white f3'>This Bot can detect different clothing items in an image. Give it a try!</p>
            <ImageLinkForm onInputChange={this.onInputChange} onButtonSubmit={this.onButtonSubmit}/>
            <ApparelDetectionLists imageUrl={this.state.imageUrl} box={this.state.box}/>
        </div>
      : ((this.state.route==='signin')
        ?  <SignIn onRouteChange={this.onRouteChange}/>
        : <Register loadUsers={this.loadUsers} onRouteChange={this.onRouteChange} />)

    }
    </div>
    );
  }
}

export default App;
